.tablet{
    @media screen and(min-width:768px) {
    padding: 0 40px;
    }
    @media screen and(min-width:1680px) {
        padding: 0 ;
    }
}

.advantages{
    // background-color: #E5E5E5;

        margin: 40px 0;

        @media screen and(min-width:768px) {
            margin: 40px 0 80px;
        }
    

    &__list{
        list-style: none;
        display: grid;
        grid-row-gap: 28px;

        @media screen and(min-width:768px) {
        grid-template-columns: repeat(3, 1fr);
        }
    }

    &__item{
        max-width: 280px;
        @media screen and(min-width:768px) {
            max-width: 215px;
        }

        @media screen and(min-width:1680px) {
            max-width: 448px;
        }
    }

    &__text{
        display: flex;
        align-items: center;

        font-family: 'Montserrat';
        font-weight: 700;
        font-size: 14px;
        line-height: 1.65;
        width: 250px;
     
        letter-spacing: 0.08em;

        @media screen and(min-width:768px) {
            width: 222px;
        }
        @media screen and(min-width:1680px) {
            width: 360px;
            font-size: 20px;
        }


        &.student::before {
            content: '';
            background-image: url(../images/Student-mob-tab.svg);
            background-size: 60px;
            width: 88px;
            height: 62px;
            background-repeat: no-repeat;
            margin-right: 12px;

            @media screen and(min-width:768px) {
                width: 104px;
            }

            @media screen and(min-width:1680px) {
                background-image: url(../images/Student-des.svg);
                background-size: 102px;
                height: 104px;
                width: 155px;
            }
        
        }

        &.mobile::before {
            content: '';
            background-image: url(../images/Mobile-mob-tab.svg);
            background-size: 60px;
            width: 60px;
            height: 62px;
            background-repeat: no-repeat;
            margin-right: 12px;

            @media screen and(min-width:1680px) {
                background-image: url(../images/Mobile-des.svg);
                background-size: 102px;
                height: 104px;
                width: 110px;
            }
        
        }

        &.insurance::before {
            content: '';
            background-image: url(../images/insurance-mob-tab.svg);
            background-size: 60px;
            width: 71px;
            height: 62px;
            background-repeat: no-repeat;
            margin-right: 12px;

            @media screen and(min-width:768px) {
                width: 60px;
            }

            @media screen and(min-width:1680px) {
                background-image: url(../images/insurance-des.svg);
                background-size: 102px;
                height: 104px;
                width: 110px;
                
            }
        
        }
    }

.student .text-decotation{

    @media screen and(min-width:1680px) {
        transform: translateX(-35px);
    }
}

    
.mobile .text-decotation{
    width: 150px;
    @media screen and(min-width:768px) {
        width: 100px;
    }
  

    @media screen and(min-width:1680px) {
    width: 205px;
    transform: translateX(-37px);
    }
}
.insurance .text-decotation{
    width: 210px;
    @media screen and(min-width:768px) {
        width: 100px;
    }
    @media screen and(min-width:1680px) {
        width: 245px;
        transform: translateX(-37px);
    }
    
}
    
}



